import { Component, OnInit } from '@angular/core';
import { menus } from '../../../app/components/menus';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpTestService } from 'src/app/http-test.service';
import * as moment from 'moment';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { MenuController, ModalController,ActionSheetController,Platform } from '@ionic/angular';
import { AboutComponent } from 'src/app/modals/about/about.component';
import { isNullOrUndefined } from 'util';
import { constants } from '../../../constants/constants';
import { GooglePlus } from '@ionic-native/google-plus/ngx';
// import { User } from '../../model/user';
@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
profile_picture_url: string;
// user:User;
navigate:[];
pictureChanged:boolean = false;
mealImage:string;
ultimamedicao:string//new Date(Date.now()); 
nome: string="";
peso: string="";
cintura:string="";
scoreTotal: number = 0;
header1: string;
header2: string;
header3: string;
toDoList: any;
id: number;
check: boolean = false;
date: any;
last_meal: boolean;
isNewUser:boolean;
hasShown:boolean = false;
  constructor(private googlePlus: GooglePlus,private route: ActivatedRoute,public platform:Platform,private camera: Camera, public actionSheetCtrl: ActionSheetController,private modalCtrl: ModalController,private menu:MenuController,private  router:  Router, private service: HttpTestService) { 
    moment.locale('pt-BR');
       
    
  }
  ionViewDidEnter(){
    
    if(window.sessionStorage.getItem('isNewUser') === String(true)&&(this.hasShown === false)){
      this.about();
      this.hasShown=true;
    }
     
    
   
    
  }
  ngOnInit() {
    // this.nome ="0";
    // this.peso =0;
    // this.cintura=1;
    
  }
  
  ionViewWillEnter(){
    
    console.log(window.sessionStorage.getItem('user_type'));
    this.navigate = menus.user;
    this.ultimamedicao = moment(Date.now()).format('L');
    this.header1 = window.sessionStorage.getItem('uid');
    this.header2 = window.sessionStorage.getItem('access-token');
    this.header3 = window.sessionStorage.getItem('client');
    console.log("ESSES SÃO OS HEADERS!"+ this.header1, this.header2, this.header3)
    this.service.getUserData('api/v1/users', this.header1, this.header2, this.header3).subscribe(resp =>{
      console.log("aqui esta o response" +JSON.stringify(resp.body));
      this.id = resp.body.user.id;
      this.nome = resp.body.user.name;
      this.nome = this.nome.split(' ').length > 2 ? this.nome.split(' ').slice(0, -1).join(' '): this.nome;
      this.scoreTotal = resp.body.client_detail_attributes.score_total !=null ? resp.body.client_detail_attributes.score_total : this.scoreTotal;
      this.profile_picture_url = resp.body.user.image_url ? `${constants.baseUrlProd}${resp.body.user.image_url}`: null
      this.peso = String(resp.body.client_physicals_attributes[(resp.body.client_physicals_attributes.length-1)].weight_kg);
      this.cintura = String(resp.body.client_physicals_attributes[(resp.body.client_physicals_attributes.length-1)].waist_cm);
      this.ultimamedicao = moment(JSON.parse(JSON.stringify(resp.body.client_physicals_attributes[(resp.body.client_physicals_attributes.length-1)].updated_at))).format('DD/MM/YYYY');
      sessionStorage.setItem('score', String(this.scoreTotal))
      if (this.peso){
        this.peso = this.peso.replace(".", ",")
      }
      if(this.cintura){
        this.cintura = this.cintura.replace(".", ",")
      }
      
    });
    this.service.getUserData('api/v1/meals', this.header1, this.header2, this.header3 ).subscribe(resp => {
      console.log("ESSE É O REQUEST DAS REFEIÇÕES" +JSON.stringify(resp.body))
      if(!resp.body.data){
      this.toDoList = resp.body;
      this.date = moment((resp.body.created_at)).format('DD/MM/YYYY');
      this.last_meal = true;
      this.mealImage = `${constants.baseUrlProd}${resp.body.image_url}`
      console.log("AGORA ESTÁ CERTO :" + (this.toDoList.description));
      }
    }); 

     
    console.log("ESSES SÃO OS HEADERS!!!!!!"+ this.header1, this.header2, this.header3)
    
    if(window.sessionStorage.getItem('google_user').length>0){
      let google_user = JSON.parse(window.sessionStorage.getItem('google_user'))
      this.nome = google_user.name
      this.profile_picture_url = google_user.picture
      this.nome = this.nome.split(' ').length > 2 ? this.nome.split(' ').slice(0, -1).join(' '): this.nome;
    }
      

 
    

  }
  ionViewWillLeave(){
    //fecha o menu independente do caminho que for, então sempre a principal carrega sem menu aberto
    this.menu.close();
  }

  doGoogleLogout(){
    this.googlePlus.logout()
    .then(res =>{
      //user logged out so we will remove him from the NativeStorage
      localStorage.setItem('google_login','false');
      this.router.navigate(["/login"]);
    }, err =>{
      console.log(err);
    })
  }

  nav(page){
    console.log(page);
    this.router.navigateByUrl(page);
  }

  async about(){
    
    const modal = await this.modalCtrl.create({
      component: AboutComponent,
      cssClass:'login_page_about_modal'
    });
    modal.onDidDismiss().then(() => {
      this.menu.close();
      this.ionViewWillEnter()
      
    });
    return await modal.present();
    
  }

  changePage(id_meal, item_description, image_file_name){
    sessionStorage.setItem('id_meal', id_meal)
    sessionStorage.setItem('description', (item_description))
    sessionStorage.setItem("image_file_name",image_file_name)
    this.router.navigateByUrl('user-meal-history');
  }

  async changePicture() {
    const actionSheet = await this.actionSheetCtrl.create({
      header: 'Alterar foto do perfil',
      buttons: [
        {
          text: 'Tirar foto',
          handler: () => {
            this.getImageFromCamera();
          }
        },
        {
          text: 'Selecionar foto da galeria',
          handler: () => {
            this.getImageFromGallery();
          }
        }
        ,{
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {}
        }
      ]
    });
    actionSheet.present();
  }

  private getImageFromGallery() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      correctOrientation: true
    }
    this.camera.getPicture(options).then((imageData) => {
      // this.user.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.pictureChanged = true;
    }, (err) => { });
  }

  private getImageFromCamera() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      sourceType: this.camera.PictureSourceType.CAMERA,
      correctOrientation: true
    }
    
    this.camera.getPicture(options).then((imageData) => {
      // this.user.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.pictureChanged = true;
      this.updatePicture();
    }, (err) => { 
      console.log(err);
    });
  }

  async updatePicture(){
    await this.service.updateData('api/v1/users',this.id,this.profile_picture_url, this.header1, this.header2, this.header3).subscribe(resp =>{
      if(resp.status === 200){
        confirm("Atualizado com sucesso.")
      }else{
        confirm("Erro ao atualizar. Tente novamente mais tarde.")
      }
     });  
  }
  
  logout(){
    if(localStorage.getItem('google_login')=='true'){
      this.doGoogleLogout()
    }
    else{
    this.service.signOut('api/auth/sign_out', this.header1, this.header2, this.header3).subscribe(resp =>{
     if(resp.status === 200){
      this.router.navigateByUrl('login');
     }else{
       confirm("Erro ao sair. Tente novamente mais tarde.")
     }
    });  
  }
    window.sessionStorage.clear();
  }

}
